.product {
  &__photo {
    width: 100%;
    @include onDesktop {
      @include hover(transform, scale(1.05));
    }
    cursor: pointer;
  }

  &__title {
    margin-top: 32px;
    font-size: 24px;
    font-weight: 700;
    margin-bottom: 8px;
  }

  &__price {
    margin-top: 16px;
    font-size: 24px;
    font-weight: 700;
  }
}
